<script>
  import { createEventDispatcher } from 'svelte';

  export let bottomHeight = 0; //Ë∑ùÁ¶ªÂ∫ïÈÉ®ÁöÑË∑ùÁ¶ª‰∏¥ÁïåÂÄºÂºÄÂßãÂà∑Êñ∞
  export let items = [];
  export let paginationHeight = 0;
  export let hasMore = true;
  export let grid = 1;
  let paginationDom;
  let paginationBoxDom;

  // Âä†ËΩΩÊõ¥Â§öËß¶ÂèëÂô® ÈªòËÆ§ÈöêËóè
  const dispatch = createEventDispatcher();

  // Âä†ËΩΩÊõ¥Â§öËß¶ÂèëÂô® ‰∫ã‰ª∂
  let loadData = function () {
    if (!items || items.length == 0) return;
    // ÂèòÈáèscrollTopÊòØÊªöÂä®Êù°ÊªöÂä®Êó∂ÔºåÊªöÂä®Êù°‰∏äÁ´ØË∑ùÁ¶ªÈ°∂ÈÉ®ÁöÑË∑ùÁ¶ª
    const scrollTop = paginationBoxDom.scrollTop;
    //ÂèòÈáèwindowHeightÊòØÂèØËßÜÂå∫ÁöÑÈ´òÂ∫¶
    const windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
    //ÂèòÈáèscrollHeightÊòØÊªöÂä®Êù°ÁöÑÊÄªÈ´òÂ∫¶ÔºàÂΩìÂâçÂèØÊªöÂä®ÁöÑÈ°µÈù¢ÁöÑÊÄªÈ´òÂ∫¶Ôºâ
    const scrollHeight = paginationDom.scrollHeight;
    // ÊªöÂä®Êù°Âà∞Â∫ïÈÉ®;
    if (scrollTop + windowHeight + bottomHeight >= scrollHeight) {
      dispatch('load');
    }
  };
  //Èò≤Êäñ
  function debounce(fn, delay) {
    let timer = null;
    return function () {
      if (timer) {
        clearTimeout(timer);
      }
      timer = setTimeout(() => {
        //Ê®°ÊãüËß¶Âèëchange‰∫ã‰ª∂
        fn.apply(this, arguments);
        // Ê∏ÖÁ©∫ËÆ°Êó∂Âô®
        timer = null;
      }, delay);
    };
  }
  // ËäÇÊµÅ
  function throttle(fn, delay = 50) {
    let timer = null;
    return function () {
      if (timer) {
        return;
      }
      timer = setTimeout(() => {
        fn.apply(this, arguments);
        timer = null;
      }, delay);
    };
  }
</script>

<!-- Âä†ËΩΩÊõ¥Â§öËß¶ÂèëÂô® - end -->
<div
  class="pagination-box"
  style="height:{paginationHeight}px"
  on:scroll={throttle(debounce(loadData, 500))}
  bind:this={paginationBoxDom}
>
  <div bind:this={paginationDom}>
    {#if items.length > 0}
      {#if grid > 1}
        <div class="grid-box" style="grid-template-columns: repeat({grid}, {300 / grid}px);">
          {#each items as item, index}
            <slot {item} {index} />
          {/each}
        </div>
      {:else}
        {#each items as item, index}
          <slot {item} {index} />
        {/each}
      {/if}
    {/if}
  </div>
  {#if hasMore}
    <div class="no-more">
      <div>
        <embed width="40" height="40" src="/images/Ripple.svg" type="image/svg+xml" />
      </div>
      <span class="no-more-text">üò§ Âà´ÊÄ•ÂïäÔºåÂú®Âä†ËΩΩ‰∫Ü...</span>
    </div>
  {:else}
    <div class="no-more no-more-text">üò§ Âà´Êªë‰∫ÜÔºåÂà∞Â∫ïÂï¶ÔºÅ</div>
  {/if}
</div>

<style>
  .pagination-box {
    overflow-y: auto;
    padding-top: 10px;
  }
  .pagination-box:global(::-webkit-scrollbar) {
    width: 0 !important;
  }
  .no-more {
    margin: 20px;
    text-align: center;
  }
  .no-more-text {
    color: #888;
  }
  .grid-box {
    display: grid;
    justify-content: space-between;
    grid-template-columns: repeat(1, 300px);
  }
</style>
